/* Footer.css */
.footer {
  background-color: #333;
  padding: 40px 20px;
  color: #6d7d6f;
}

.footer-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1200px;
  margin: 0 auto;
  flex-wrap: wrap;
}

.footer-logo a {
  color: white;
  font-size: 1.5rem;
  text-decoration: none;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 900px;
}

.heading {
  font-size: 20px;
  margin-right: 15px;
  color: rgb(246, 187, 187);
  padding:10px;
}

.footer-section {
  margin-right: 30px;
}

.footer-section h4 {
  margin-bottom: 15px;
  font-size: 16px;
  color: black;
}

.footer-section ul {
  list-style: none;
  padding: 0;
}

.footer-section ul li {
  margin-bottom: 10px;
}

.footer-section ul li a {
  color: white;
  text-decoration: none;
  font-size: 14px;
}

.footer-section ul li a:hover {
  text-decoration: underline;
}

@media (max-width: 768px) {
  .footer-container {
    flex-direction: column;
    align-items: flex-start;
  }

  .footer-content {
    flex-direction: column;
  }

  .footer-section {
    margin-right: 0;
    margin-bottom: 20px;
  }
}
